let Question = (props) => {
    let [isOpen, setIsOpen] = useState(false);

    let ref__answer = React.createRef();
    return (
        <div style={{marginBottom: 12, boxShadow: '0px 4px 16px rgba(37, 38, 50, 0.8)'}}>
            <div className={styles.question__block} onClick={() => {
                // console.log(ref__answer)
                // console.log(document.querySelector(`.${styles.answer__block}`).offsetTop
                console.dir(document.querySelector(`.${styles.ans}`))
                if (!isOpen) {
                    ref__answer.current.style.height = `${document.querySelector(`.${styles.ans}`).clientHeight + 32}px`;
                } else ref__answer.current.style.height = `0px`;
                setIsOpen(!isOpen)
            }} style={{
                color: isOpen ? "#FFF" : "",
                background: isOpen ? "#39394A" : "",
                borderBottom: isOpen ? "1px solid #48485C" : "",
                borderRadius: isOpen ? "3px 3px 0px 0px" : ""
            }} >
                <span>{props.title}</span>
                <img src={down} className={styles.q_img} style={{
                    opacity: !isOpen ? 1 : 0,
                }}/>
                <img src={top} className={styles.q_img} style={{
                    opacity: isOpen ? 1 : 0,
                    transform: !isOpen ? "rotate(180deg)" : "rotate(0deg)"
                }}/>
            </div>
            <div className={styles.answer__block} style={{
                // height: !isOpen ? 0 : "", 
                overflow: "hidden",
                // opacity: isOpen ? 1 : 0
                // display: isOpen ? "flex" : "none"
                // padding: !isOpen ? 0 : ""
                }} ref={ref__answer}>
                <div style={{margin: 13}} className={styles.ans}>
                    {props.answer}
                </div>
            </div>
        </div>
    )
}

