let FAQ = (props) => {
    let [SideIsFixed, setSideIsFixed] = useState(false);
    let [isSide, setIsSide] = useState(true);   

    let [filter, setFilter] = useState("");

    useEffect(() => {
        setIsSide(document.documentElement.clientWidth >= 1527)
        window.addEventListener('resize', (event) => {
            console.log('window.pageXOffset:',document.documentElement.clientWidth)
            setIsSide(document.documentElement.clientWidth >= 1527)
        });
        window.addEventListener('scroll', (event) => {

            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 95 ) {
                setSideIsFixed(true)
            } else setSideIsFixed(false)
        })
    }, [])
    return (
        <div className={s.wrapper} style={
            (SideIsFixed && isSide) ? {marginLeft: 305} :{}
        }>
            <BetsSidebar/>
            <div className={s.bets_main}>
                <div className={s.bets__content}>
                    <div className={styles.stat__container}>
                            <div className={styles.stat__header}>
                                <div className={styles.header__title}>
                                    FAQ (    )
                                </div>
                                <div className={styles.input__wrapper}>
                                    <img src={search} alt="" className={styles.title__search}/>
                                    <input type="text" className={styles.title__input} placeholder="   FAQ" style={{width: 200}}
                                        onChange={(event) => setFilter(event.target.value)}/>
                                </div>
                            </div>
                    </div>
                    <div className={styles.container}>
                        {
                            filter ? 
                            FAQ__data.map(e => {
                                return (
                                    <div>
                                    {
                                        e.questions.map(question => {
                                            if (question.title.toLowerCase().indexOf(filter.toLowerCase()) === -1) return;
                                            return (
                                                <Question {...question}/>
                                            )
                                        })
                                    }
                                    </div>
                                )
                            })
                            :
                            FAQ__data.map(e => {
                                return (
                                    <div style={{display: "flex", flexDirection: "column"}}>
                                        <div className={styles.section__title}>{e.title}</div>
                                        {
                                            e.questions.map(question => {
                                                return (
                                                    <Question {...question}/>
                                                )
                                            })
                                        }
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
            <div>
                <Inventory selectedCoef = {null} isReg = {props.isReg}
                    setIsReg = {props.setIsReg}/>
            </div>
            <Footer/>
        </div>
    )
}

export {FAQ};