import React, {useEffect, useState} from 'react';
import styles from './match.module.css';

import live from './img/live.png';
import search from './img/search.png'
import more from './img/more.png';

import {Match} from './Match';

const EMBED_URL = 'https://embed.twitch.tv/embed/v1.js';


let CurrentMatch = (props) => {
    let [filter, setFilter] = useState("");
    console.log('match = ',props.match)
    useEffect(() => {
        const script = document.createElement('script');
        script.setAttribute(
            'src',
            EMBED_URL
          );
        document.body.appendChild(script);
    }, [])
    useEffect(() => {
        console.log("______UPDATE_MATCHES______")
        console.log(props.matches)
    }, [props.matches])
    let [isMore, setIsMore] = useState(false);
    return (
        <div className={styles.match__wrapper} style={{marginTop: 22}}>
            <div className={styles.match__block__title}>
                <div className={styles.title__left}>
                    <img src={live} alt=""/>
                    <span className={styles.title__value}> </span>
                </div>
                <div className={styles.input__wrapper}>
                    <img src={search} alt="" className={styles.title__search}/>
                    <input type="text" className={styles.title__input} placeholder=" " onChange={(event) => setFilter(event.target.value)}/>
                </div>
            </div>
            <div className={styles.match__container} >
                {props.matches.map((e, index) => {
                    if (`${e.left_name}${e.right_name}`.toLowerCase().indexOf(filter.toLowerCase()) === -1) return;
                    if (!isMore) if (index > 5) return;
                   return <Match
                                needUpdate = {props.needUpdate}
                                setNeedUpdate = {props.setNeedUpdate}
                                location = {props.location}
                                {...e}
                                selectedCoef = {props.selectedCoef}
                                setSelectedCoef = {props.setSelectedCoef}
                                index={index}
                          />
                })}
            </div>
            <div className={styles.button__more} onClick={() => setIsMore(!isMore)}>   
                <div>
                    
                </div>
                {/* <div> */}
                    <img src={more} alt='' style={{
                        transform: isMore ? "rotate(180deg)" : "",
                        transition: ".3s transform ease-in-out"
                    }}/>
                {/* </div> */}
            </div>
        </div>
    )
}

export {CurrentMatch};