import React, {useState, useEffect} from 'react';

import s from './../AllBets/AllBets.module.css';
import styles from './Help.module.css';

import {BetsSidebar} from "../BetsSidebar/BetsSidebar";
import {Inventory} from "../AllBets/parts/Inventory";
import {Footer} from "../Footer/Footer";

import image from './img/photo.png'

let Help = (props) => {
    let [SideIsFixed, setSideIsFixed] = useState(false);
    let [isSide, setIsSide] = useState(true);

    let [rememberMe, setRememberMe] = useState(false);

    let ref__input = React.createRef();

    useEffect(() => {
        setIsSide(document.documentElement.clientWidth >= 1527)
        window.addEventListener('resize', (event) => {
            console.log('window.pageXOffset:',document.documentElement.clientWidth)
            setIsSide(document.documentElement.clientWidth >= 1527)
        });
        window.addEventListener('scroll', (event) => {

            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 95 ) {
                setSideIsFixed(true)
            } else setSideIsFixed(false)
        })
    }, [])
    return (
        <div className={s.wrapper} style={
            (SideIsFixed && isSide) ? {marginLeft: 305} :{}
        }>
            <BetsSidebar/>
            <div className={s.bets_main}>
                <div className={s.bets__content}>
                    <div className={styles.stat__container}>
                        <div className={styles.stat__header}>
                            <div className={styles.header__title}>
                                   
                            </div>
                        </div>
                        <div className={styles.main__container}>
                            <div className={styles.data__container}>
                                <div className={styles.data__title}>
                                      
                                </div>
                                <div className={styles.data__desc}>
                                    .      .        
                                </div>
                                <input className={styles.input} placeholder="  "/>
                                <div className={styles.textarea__container}>
                                    <textarea className={styles.textarea} 
                                        placeholder="  .     -   !     -      ."/>
                                    <div className={styles.textarea__title}>
                                            E-Mail,   
                                    </div>
                                    <input type="file" name="photo" id="upload_photo" accept="image/*" style={{
                                            opacity: 0,
                                            position: "absolute",
                                            right: 16,
                                            bottom: 16,
                                            zIndex: 0,
                                            overflow: 'hidden',
                                        }} onChange={(event) => {
                                            event.persist();
                                                console.log(ref__input.current)
                                                ref__input.current.style.filter = 
                                                event.target.files.length ? 'invert(83%) sepia(29%) saturate(6531%) hue-rotate(0deg) brightness(105%) contrast(103%)' : ""
                                                // console.log('event = ',event.target.files.length)
                                            }}/>
                                    <label for="upload_photo" style={{
                                            position: 'absolute',
                                            right: 16,
                                            bottom: 16,
                                            overflow: 'hidden'
                                        }}><img src={image} ref={ref__input}/></label>
                                </div>
                                <div className={styles.bottom__container}>
                                    <div className={styles.checkbox__container}>
                                        <div className={rememberMe ? styles.checkbox_true : styles.checkbox_false}
                                        onClick={() => setRememberMe(!rememberMe)}/>
                                        <div>    <span style={{color: "#FFF"}}>FAQ</span></div>
                                    </div>
                                    <div className={styles.button}>
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <Inventory selectedCoef = {null} isReg = {props.isReg}
                    setIsReg = {props.setIsReg}/>
            </div>
            <Footer/>
        </div>
    )
}

export {Help};