import React, {useEffect, useState} from "react";
import {Redirect} from "react-router";

import styles from './BetsSidebar.module.css'

import s_style from './../AllBets/parts/match.module.css'

import circle from '../AllBets/img/circle.png';
import forze_logo from '../AllBets/img/forze_logo.png'
import something_logo from '../AllBets/img/something_logo.png'
import live from '../AllBets/img/live.png'

import win_left from '../AllBets/img/win_left.png'
import win_right from '../AllBets/img/win_right.png'
import lose_left from '../AllBets/img/lose_left.png'
import lose_right from '../AllBets/img/lose_right.png'

let leftWin = [true, false, false, true];

let BetsSidebar = (props) => {
    let [needRed, setNeedRed] = useState(false);
    let [isFixed, setIsFixed] = useState(false);

    useEffect(() => {
        window.addEventListener('scroll', (event) => {
            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 95) {
                setIsFixed(true)
            } else setIsFixed(false)
        })
    }, [])
    let getCloseMatches = () => {
        let content = [];
        for (let i = 0; i < 4; i++) {
            content.push(<a className={styles.close__matches} onClick={() => {
                if (!props.setNeedUpdate) setNeedRed(true);
                if (!props.setNeedUpdate) {
                    setTimeout(() => {
                        document.querySelectorAll(`.${s_style.match__one}`)[i].scrollIntoView();
                        window.setNeedUpdate = i;
                    }, 100)
                    return;
                } else {
                    props.setNeedUpdate(i)
                    document.querySelectorAll(`.${s_style.match__one}`)[i].scrollIntoView();
                }
            }}>
                <img src={forze_logo} alt=""/>
                <div className={styles.close__middle}>
                    <div className={styles.close__time}>22:00</div>
                    <div className={styles.close__bottom}>Cis Time</div>
                </div>
                <img src={something_logo} alt=""/>
            </a>)
        }
        return content;
    }

    let getCompletedMatches = () => {
        return leftWin.map((isLeft,index) => {
            return (
                <a className={styles.completed__matches}>
                    <img src={isLeft ? win_left : lose_left} className={styles.completed__left} alt=""/>
                    <img src={forze_logo} alt=""/>
                    <div className={styles.close__middle}>
                        <div className={styles.close__time}>
                            <span style={{opacity: isLeft ? 1 : 0.3}}>16</span> :&nbsp;
                            <span style={{opacity: isLeft ? 0.3 : 1}}>1</span>
                        </div>
                    </div>
                    <img src={something_logo} alt=""/>
                    <img src={isLeft ? lose_right : win_right} className={styles.completed__right} alt=""/>
                </a>
            )
        })
    }
    if (needRed) return <Redirect to='/AllBets'></Redirect>
    return (
        <div className={styles.sidebar__wrapper} style={
            isFixed ? {
                position: 'fixed',
                top: 0,
                left: 70,
            } : {}
        }>
            <div className={styles.block__1}>
                <div className={styles.block__title}>
                    <span> </span>
                    <span><img src={circle} alt=""/></span>
                </div>
                <a className={styles.current__matches} onClick={() => {
                    if (!props.setNeedUpdate) setNeedRed(true);
                    if (!props.setNeedUpdate) return;
                    props.setNeedUpdate(4)
                    document.querySelectorAll(`.${s_style.match__one}`)[4].scrollIntoView();
            }}>
                    <img src={forze_logo} alt=""/>
                    <div className={styles.live__button}>
                        <img src={live} alt=""/>
                        <span>Live</span>
                    </div>
                    <img src={something_logo} alt=""/>
                </a>
            </div>
            <div className={styles.block__1}>
                <div className={styles.block__title} style={{marginBottom: 4}}>
                    <span> </span>
                </div>
                {getCloseMatches()}
            </div>
            <div className={styles.block__1}>
                <div className={styles.block__title} style={{marginBottom: 4}}>
                    <span> </span>
                </div>
                {getCompletedMatches()}
            </div>
        </div>
    )
}

export {BetsSidebar};