let Option = (e) => {
    console.log('Option')
    let [isOpen, setIsOpen] = useState(false);
    useEffect(() => {
        if (isOpen) {
            setIsOpen(false)
        }
    }, [e.isOpen])

    return (
        <div className={styles.match__options}>
            {e.active_coef &&
            <div className={styles.option_win_line}
                 style={{left: e.active_coef === 'left' ? 0 : "", right: e.active_coef === 'right' ? 0 : "",
                     background: e.active_win ? "#399241" : "#A93B3D"}}>

            </div>
            }
            {e.active_coef &&
            <div
                className={e.active_coef === 'left' ? styles.option_button_left : styles.option_button_right}
                // onClick={() => setIsOpen(!isOpen)}
                onMouseOver={() => setIsOpen(true)}
                onMouseOut={(event) => {
                    event.persist()
                    console.log('OVER', event)
                    setIsOpen(false)
                }}
            >
                {e.active_win ? "+" : "-"}{e.weapons.length}

            </div>
            }
            {(e.active_coef && isOpen) &&
            // <div className={styles.option__list}>
            <div className={styles.option__list__wrapper} style={ e.active_coef === 'left' ? {left: 23} : {right: 23}}>
                {e.weapons.map(o => {
                    return <img src={require(`./img/weapons/${o}`)} alt="" className={styles.option__weapon}/>
                })}
            </div>
                // </div>
            }
            {e.active_coef &&
            <div className={e.active_coef === 'left' ? styles.option__title_left : styles.option__title_right}>
                <img src={require(`./img/weapons/${e.title_weapon}`)} alt=""/>
            </div>
            }
            <div className={styles.team_name} style={{justifyContent: "flex-end", marginRight: 16}}>{e.left_value}</div>
            <div className={styles.block__coef__title}
                 style={{background: e.active_coef === "left" ? e.active_win ? "#399241" : "#A93B3D" : ""}}>
                {e.left_coef}
            </div>
            <div className={styles.match__result}>
                {e.center_text}
            </div>
            <div className={styles.block__coef__title}
                 style={{background: e.active_coef === "right" ? e.active_win ? "#399241" : "#A93B3D" : ""}}>
                {e.right_coef}
            </div>
            <div className={styles.team_name} style={{justifyContent: "flex-start", marginLeft: 16}}>{e.right_value}</div>
        </div>
    )
}


let Match = (props) => {
    let [isOpen, setIsOpen] = useState(false)
    let [hover, setHover] = useState(false);
    let ref_options = React.createRef()
    console.log(props.options)
    let handleOption = (b) => {
        // console.table([ref_options.current])
        // console.log(ref_options.current.scrollHeight)
        // let timeout;
        if (b) {
            window.timeout = setTimeout(() => {
                console.log('index=',props.index)
                if (document.querySelectorAll(`.${styles.options_wrapper}`)[props.index]) document.querySelectorAll(`.${styles.options_wrapper}`)[props.index].style.overflow = 'visible';
            }, 500)
            ref_options.current.style.height = `${ref_options.current.scrollHeight}px`;
        }
        else {
            clearTimeout(window.timeout)
            ref_options.current.style.overflow = 'hidden';
            ref_options.current.style.height = `0px`;
        }
    }
    return (
        <div  style={{marginBottom: 12}}>
            <div className={styles.match__one} style={{background: (isOpen) ? "#39394a" : ""}} onClick={(event) => {
            event.stopPropagation()
            handleOption(!isOpen)
            setIsOpen(!isOpen)
            console.log(isOpen)
        }} onMouseMove={() => setHover(true)} onMouseLeave={() => setHover(false)}>

            <div className={styles.match__team}
                 style={{background: "linear-gradient(270deg, rgba(47, 47, 63, 0.5) 0%, #4E426E 135.99%)",
                     opacity: isOpen ? 0 : 1}}>
                <img src={require(`./img/teams/left.png`)} alt="" className={styles.match__img}/>
            </div>
            <div className={styles.match__team}
                 style={{background: "linear-gradient(270deg, rgba(59, 58, 79, 0.5) 0%, #4E426E 135.99%)",
                     opacity: (isOpen || hover) ? 1 : 0}}>
                <img src={require(`./img/teams/left.png`)} alt="" className={styles.match__img}/>
            </div>
            <img src={require(`./img/teams/cs_grey.png`)} alt="" className={styles.mini_game_logo}/>
            <div className={styles.team_name} style={{justifyContent: "flex-end"}}>
                {props.left_team}
            </div>
            <div  className={styles.logo__team} style={{
                borderRadius: 5,
                // border:`2px solid ${props.message ? "#2f2f3f" : props.right_win ? "#399241" : "#A93B3D"}`
            }}>
                <img src={require(`./img/teams/${props.logo_left}`)} alt=""/>
            </div>
            <div className={styles.block__winLose}
                 // style={{color: props.left_win ? "#399241" : "#A93B3D"}}
            >
                {/*{!props.message ? props.left_win ? "Win" : "Lose" : ""}*/}
                {/*{props.left_coef}*/}
                <div className={styles.block__coef__title} style={{fontSize: 13}}>
                    {props.left_coef}
                </div>
            </div>
            {!props.message ?
                <div className={styles.match__result} style={{
                    fontWeight: 'bold',
                    fontSize: 24,
                    // lineHeight: 27,
                    // height: "100%",
                    zIndex: 10,
                }}>
                    <span style={{color: props.left_win ? "#FFF" : "#71768B"}}>{props.left_points}</span>
                    &nbsp;:&nbsp;
                    <span style={{color: props.right_win ? "#FFF" : "#71768B"}}>{props.right_points}</span>
                </div>
                 :
                 <div className={styles.match__result} style={{
                    fontStyle: 'normal',
                    fontWeight: 'normal',
                    fontSize: 18,
                    // lineHeight: 21,
                    color: "#71768B",
                    width: 212,
                    textTransform: "uppercase"
                }}>
                    {props.message}
                </div>
            }
            <div className={styles.block__winLose}
                 // style={{color: props.right_win ? "#399241" : "#A93B3D"}}
            >
                {/*{!props.message ? props.right_win ? "Win" : "Lose" : ""}*/}
                {
                    /*{props.right_coef}*/}
                <div className={styles.block__coef__title} style={{fontSize: 13}}>
                    {props.right_coef}
                </div>
            </div>
            <div  className={styles.logo__team}  style={{
                borderRadius: 5,
                // border:`2px solid ${props.message ? "#2f2f3f" : props.right_win ? "#399241" : "#A93B3D"}`
            }}>
                <img src={require(`./img/teams/${props.logo_right}`)} alt=""/>
            </div>
            <img src={bottom_open} className={styles.bottom_open} onClick={(event) => {
            }}/>
            <img src={arrow_down} alt="" className={styles.bottom_arrow}
                 style={{transform: !isOpen ? "translateX(-50%) rotate(0deg)" : " translateX(-50%) rotate(180deg)", opacity: !isOpen ? 1 : 0}}
            />
            <img src={arrow_up} alt="" className={styles.bottom_arrow}
                 style={{transform: isOpen ? "translateX(-50%) rotate(0deg)" : " translateX(-50%) rotate(-180deg)", opacity: isOpen ? 1 : 0}}
            />
            <div className={styles.team_name} style={{justifyContent: "flex-start"}}>
                {props.right_team}
            </div>
            <div className={styles.completed_date}>
                <div>{props.time}</div>
                <div style={{fontSize: 12}}>{props.date}</div>
            </div>
            {/*<div className={styles.match__team}*/}
            {/*     style={{background: "linear-gradient(270deg, rgba(59, 58, 79, 0.5) 0%, #4E426E 135.99%)",*/}
            {/*         opacity: isOpen ? 1 : 0}}>*/}
            {/*    <img src={require(`./img/teams/${'1.png'}`)} alt="" className={styles.match__img}/>*/}
            {/*</div>*/}
        </div>
            <div className={styles.options_wrapper} ref={ref_options} style={{overflow: "hidden"}}>
                {props.options.map((e, index) => {
                    return <Option {...e} isOpen={!isOpen} index={index}/>
                })}
            </div>
        </div>
    )
}

let CompletedMatch = (props) => {
    let [filter, setFilter] = useState("");
    let [allMatches, setAllMatches] = useState(props.matches)
    let [isMore, setIsMore] = useState(false);
    useEffect(() => {
        setAllMatches(props.matches);
    }, props.matches)
    return (
        <div className={styles.match__wrapper}>
            <div className={styles.match__block__title}>
                <div className={styles.title__left}>
                    <img src={completed_match} alt=""/>
                    <span className={styles.title__value}> </span>
                </div>
                <div className={styles.input__wrapper}>
                    <img src={search} alt="" className={styles.title__search}/>
                    <input type="text" className={styles.title__input} placeholder=" " onChange={(event) => setFilter(event.target.value)}/>
                </div>
            </div>
            <div className={styles.match__container}>
                {
                    allMatches.map((e, index) => {
                        if (!isMore) if (index > 2) return;
                        // if (`${e.left_team}${e.right_team}`.toLowerCase().indexOf(filter.toLowerCase()) === -1) return;
                        return <Match {...e}/>
                    })
                }
            </div>
            <div className={styles.button__more} onClick={() => setIsMore(!isMore)}>   
                <div>
                    
                </div>
                {/* <div> */}
                    <img src={more} alt='' style={{
                        transform: isMore ? "rotate(180deg)" : "",
                        transition: ".3s transform ease-in-out"
                    }}/>
                {/* </div> */}
            </div>
        </div>
    )
}

export {CompletedMatch}