import React from 'react';

import styles from './Footer.module.css'

import logo from './img/logo.png'
import steam from './img/steam.png'
import {NavLink} from 'react-router-dom'

let Footer = () => {
    return (
        <div className={styles.footer} style={document.documentElement.clientWidth < 1527 ? {marginLeft: 70, width: "calc(100% - 70px)"} : {}}>
            <div className={styles.logo__wrapper}>
                <img src={logo} alt=""/>
                <span>  </span>
            </div>
            <div className={styles.menu}>
                {/* <span></span> */}
                <NavLink to='/Agreement'><span></span></NavLink>
                <NavLink to='/FAQ'><span>FAQ</span></NavLink>
            </div>
            <div className={styles.steam__wrapper}>
                <img src={steam} alt=""/>
                <span>  </span>
            </div>
        </div>
    )
}

export {Footer}