let Input = (props) => {
    let [focus, setFocus] = useState(false);
    return (
        <div className={styles.input__container}>
            <div className={styles.edit__title} style={{color: focus ? "#FCB90B" : ""}}>{props.title}</div>
            <input placeholder={`${props.placeholder}`} className={styles.input__edit}
                onFocus = {() => setFocus(true)}
                onBlur = {() => setFocus(false)}
            />
            {/* <div className={styles.input__logo}> */}
            {!props.value &&
                <img className={styles.input__logo} src={input_edit} style={{opacity: focus ? 0 : 1}}/>
            }
            {!props.value &&
                <img className={styles.input__logo} src={input_edit_yellow} style={{opacity: focus ? 1 : 0}}/>
            }
            {/* </div> */}
        </div>
    )
}

let InputCode = (props) => {
    let [focus, setFocus] = useState(false);
    let [value, setValue] = useState("");
    let [max, setMax] = useState("");
    let inputRef = React.createRef();
    // useEffect(() => {
    //     document.addEventListener("click", (event) => {
    //         console.log((event.target.className === styles.input__code || event.target.className === styles.code__value))
    //         setFocus((event.target.className === styles.input__code || event.target.className === styles.code__value))
    //     });
    //     document.addEventListener('keydown', (event) => {
    //         alert(focus)
    //         if (focus) {
    //             setValue(value + event.key)
    //         }
    //     })
    // }, [])
    return (
        <div style={{position: "relative"}}>
            <div className={styles.edit__title} style={{marginLeft: 70}}>  </div>
            <input className={styles.input__code} ref={inputRef} maxLength={7} value={max} style={{
                caretColor: max.length === 7 ? "#252632" : ""
            }} onChange={(event) => {
                if ("123456789".indexOf(event.target.value[event.target.value.length - 1]) === -1 
                && event.target.value.length !== 0 && event.target.value[event.target.value.length - 1] !== "~") return;
                if (max.length === 2 && event.target.value.length === 3) setMax(max + event.target.value[event.target.value.length - 1] + "~")
                else setMax(event.target.value)
                // console.log(inputRef.current.value)}
            }}  />
                {/* <input/> */}
            <div className={styles.code__wrapper}>
                <div className={max.length >= 1 ? styles.code__value__active : styles.code__value}></div>
                <div className={max.length >= 2 ? styles.code__value__active : styles.code__value}></div>
                <div className={max.length >= 3 ? styles.code__value__active : styles.code__value}></div>
                <div className={max.length >= 4 ? styles.code__value__active : styles.code__value}></div>
                <div className={max.length >= 5 ? styles.code__value__active : styles.code__value}></div>
                <div className={max.length >= 6 ? styles.code__value__active : styles.code__value}></div>
                <div className={max.length >= 7 ? styles.code__value__active : styles.code__value}></div>
            </div>
            <div className={styles.code__button}>
                    <div className={styles.cButton}>
                    </div>
                    <div className={styles.cButton__value}>
                        
                    </div>
                </div>            
        </div>
    )
}

