let Bet = (props) => {
    return (
        <div className={styles.bet__container}>
            <div className={props.result ? styles.bet__top__win : styles.bet__top__lose}>
                <div className={styles.bet__date}>
                    <span style={{marginRight: 14}}>{props.date}</span> 
                    <span>{props.time}</span>
                </div>
                <div className={styles.bet__discipline}>
                    <img src={csgo_logo}/>
                    <div className={styles.discipline__name}>
                        {props.discipline}
                    </div>
                </div>
                <div className={styles.total__sum}>
                    <div className={styles.bet__count} style={{color: props.result ? "" : "#FF8E91"}}>
                        {props.result ? "+" : "-"} {props.weapons.length} Items
                    </div>
                    <div className={styles.sum__value} style={{color: props.result ? "" : "#FF8E91"}}>
                        {props.totalPrice}
                    </div>
                    <img src={props.result ? dollar__win : dollar__lose}/>
                </div>
                <div className={styles.bet__result__container}>
                    <div className={styles.bet__name} style={{marginRight: 10}}>
                        {props.left_team}
                    </div>
                    <div className={styles.logo__wrapper} style={{
                        marginRight: 10, 
                        border: `2px solid ${props.left_win ? "#399241" : "#A93B3D"}`
                        }}>
                        <img src={require(`./img/teams/${props.left_logo}`)}/>
                    </div>
                    <div className={styles.bet__winLose} style={{marginRight: 20, color: props.left_win ? "#399241" : "#A93B3D"}}>
                        {props.left_win ? "WIN" : "LOSE"}
                    </div>
                    <div className={styles.bet__point}>
                        <span style={{color: !props.left_win ? "#71768b" : ""}}>{props.left_point}</span> : <span style={{color: props.left_win ? "#71768b" : ""}}>{props.right_point}</span>
                    </div>
                    <div className={styles.bet__winLose} style={{marginLeft: 20, color: !props.left_win ? "#399241" : "#A93B3D"}}>
                        {!props.left_win ? "WIN" : "LOSE"}
                    </div>
                    <div className={styles.logo__wrapper} style={{
                        marginLeft: 10, 
                        border: `2px solid ${!props.left_win ? "#399241" : "#A93B3D"}`
                        }}>
                        <img src={require(`./img/teams/${props.right_logo}`)}/>
                    </div>
                    <div className={styles.bet__name} style={{marginLeft: 10}}>
                        {props.right_team}
                    </div>
                </div>
            </div>
            <div className={styles.weapons__container}>
                {props.weapons.map(e => {
                    return (
                        <div className={styles.weapon__wrapper} style={{
                            background: props.result ? 
                                            'radial-gradient(50% 50% at 50% 50%, rgba(57, 146, 65, 0.2) 0%, rgba(57, 146, 65, 0) 100%), #2F2F3F'
                                        :
                                        'radial-gradient(50% 50% at 50% 50%, rgba(169, 59, 61, 0.2) 0%, rgba(169, 59, 61, 0) 100%), #2F2F3F',
                        }}>
                            <img src={require(`./img/weapons/${e}`)} className={styles.weapon_img} style={{
                                opacity: props.result ? 1 : 0.6
                            }}/>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

