let Main = (props) => {
    return (
        <div className={styles.main}>
                    <div className={styles.user__panel}>
                        <div className={styles.user__header}>
                            <span>  </span>
                            <img src={steam}/>
                        </div>
                        <div className={styles.photo__container}>
                            <div className={styles.photo__wrapper}>
                                <img src={logo}/>
                            </div>
                        </div>
                        <div className={styles.user__name}>
                            rimelovepie        
                        </div>
                        <div className={styles.user__email}>
                            mediarime@gmail.com
                        </div>
                        <div className={styles.buttons__container}>
                            <div className={styles.button_in}>
                                
                            </div>
                            {/* <div className={styles.button_out}>
                                  
                            </div> */}
                        </div>
                        <div className={styles.line}></div>
                        <div className={styles.user__data__container}>
                            <div className={styles.data__line}>
                                <div className={styles.data__left}>
                                     :
                                </div>
                                <div className={styles.data__right}>
                                    18.09.2019
                                </div>
                            </div>
                            <div className={styles.data__line}>
                                <div className={styles.data__left}>
                                     :
                                </div>
                                <div className={styles.data__right}>
                                    21 447
                                </div>
                            </div>
                            <div className={styles.data__line}>
                                <div className={styles.data__left}>
                                    :
                                </div>
                                <div className={styles.data__right}>
                                    RU
                                </div>
                            </div>
                            <div className={styles.data__line}>
                                <div className={styles.data__left}>
                                    :
                                </div>
                                <div className={styles.data__right}>
                                    1 472
                                </div>
                            </div>
                            <div className={styles.data__line}>
                                <div className={styles.data__left}>
                                     :
                                </div>
                                <div className={styles.data__right}>
                                    48%
                                </div>
                            </div>

                        </div>
                    </div>
                    <div className={styles.user__right}>
                        <div className={styles.right__header}>
                            <div className={styles.right__item} onClick={() => props.setRightPage(1)} style={{
                                borderBottom: props.rightPage === 1 ? '2px solid #FCB90B' : "",
                                color: props.rightPage === 1 ? "#FFF" : ""
                            }}>
                                
                            </div>
                            <div className={styles.right__item} onClick={() => props.setRightPage(2)} style={{
                                borderBottom: props.rightPage === 2 ? '2px solid #FCB90B' : "",
                                color: props.rightPage === 2 ? "#FFF" : ""
                            }}>
                                 
                            </div>
                        </div>
                        {
                            props.rightPage === 1 && 
                            <div className={styles.container__item}>
                                {
                                    items.map(e => {
                                    return (
                                        <Item {...e}/>
                                    )
                                    })
                                }
                            </div>
                        }
                        {
                            props.rightPage === 2 && 
                            <div className={styles.container__bets}>
                                {
                                    bets.map(e => {
                                        return <Bet {...e}/>
                                    })
                                }
                            </div>
                        }
                    </div>
                </div>
    )
}

