let AllBets = (props) => {
    let [selectedCoef, setSelectedCoef] = useState(null)
    let [currentMatches, setCurrentMatches] = useState(matches);
    let [completedMatches, setCompletedMatches] = useState(completedMatches2)
    console.log('location = ',props.location)
    let [SideIsFixed, setSideIsFixed] = useState(false);
    let [isSide, setIsSide] = useState(true);
    let [needUpdate, setNeedUpdate] = useState(null);
    let setCurMatches = (newMatches) => {
        setCurrentMatches(newMatches)
    }
    let addCurrentMatch = (match, isTop = true) => {
        if (isTop) {
            setCurrentMatches([match, ...currentMatches]) 
        } else {
            setCurrentMatches([...currentMatches, match])
        }
    }
    let setCurrentMatch = (idMatch, newMatch) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                newMatches[i] = newMatch;
                isFind = true;
                break;
            }
        }
        if (!isFind) {
            alert(`match with id = ${idMatch} was not found`)
        } else {
            setCurrentMatches(newMatches);
        }
    }
    let setCurrentMatchCoef = (idMatch, coefs, changeDynamic = true) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                if (coefs.left_coef) {
                    if (changeDynamic) {
                        if (coefs.left_coef > newMatches[i].left_coef) newMatches[i].left_green = true 
                        else if (coefs.left_coef < newMatches[i].left_coef) newMatches[i].left_green = false 
                    }
                    newMatches[i].left_coef = coefs.left_coef;
                    
                }
                if (coefs.right_coef) {
                    if (changeDynamic) {
                        if (coefs.right_coef > newMatches[i].right_coef) newMatches[i].right_green = true 
                        else if (coefs.right_coef < newMatches[i].right_coef) newMatches[i].right_green = false 
                    }
                    newMatches[i].right_coef = coefs.right_coef;
                }
                break;
            }
        }
        if (!isFind) {
            alert(`match with id = ${idMatch} was not found`);
        } else {
            setCurrentMatches(newMatches)
        }
    }
    let removeCurrentMatch = (idMatch) => {
        let newMatches = [...currentMatches];
        let index = null;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                index = i;
            }
        }
        if (index === null) alert(`match with id = ${idMatch} was not found`) 
        else {
            newMatches.splice(index, 1);
            setCurrentMatches(newMatches);
        }
    }
    let addOption = (idMatch, option, isTop = true) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                if (isTop) newMatches[i].options.unshift(option) 
                else newMatches[i].options.push(option);
                setCurrentMatches(newMatches);
                break;
            }
        }
        if (!isFind) alert(`match with id = ${idMatch} was not found`);
    }
    let addCompletedMatch = (match, isTop = true) => {
        // alert('nice')
        if (isTop) setCompletedMatches([match, ...completedMatches]) 
        else setCompletedMatches([...completedMatches, match]); 
    }
    let setComMatches = (newMatches) => {
        setCompletedMatches(newMatches);
    }
    let setCoefInOption = (idMatch, index, coefs) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                if (coefs.left_coef) newMatches[i].options[index].left_coef = coefs.left_coef;
                if (coefs.right_coef) newMatches[i].options[index].right_coef = coefs.right_coef;
                setCurrentMatches(newMatches);
                break;
            }
        }
        if (!isFind) alert(`match with id = ${idMatch} was not found`);
    }
    let setMatchWatch = (idMatch, isWatch) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                newMatches[i].watch = isWatch;
                setCurrentMatches(newMatches);
            }
        }
        if (!isFind) alert(`match with id = ${idMatch} was not found`);
    }
    let setMatchLive = (idMatch, isLive) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                newMatches[i].center_text = "";
                newMatches[i].date = null;
                newMatches[i].live = isLive;
                setCurrentMatches(newMatches);
            }
        }
        if (!isFind) alert(`match with id = ${idMatch} was not found`);
    }
    let setMatchCenterText = (idMatch, centerText) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                newMatches[i].center_text = centerText;
                newMatches[i].date = null;
                newMatches[i].live = false;
                setCurrentMatches(newMatches);
            }
        }
        if (!isFind) alert(`match with id = ${idMatch} was not found`);
    }
    let setMatchDate = (idMatch, date) => {
        let newMatches = [...currentMatches];
        let isFind = false;
        for (let i = 0; i < newMatches.length; i++) {
            if (newMatches[i].id === idMatch) {
                isFind = true;
                newMatches[i].center_text = "";
                newMatches[i].date = date;
                newMatches[i].live = false;
                setCurrentMatches(newMatches);
            }
        }
        if (!isFind) alert(`match with id = ${idMatch} was not found`);
    }
    window.setMatchDate = setMatchDate;
    window.setMatchCenterText = setMatchCenterText;
    window.setMatchLive = setMatchLive;
    window.setMatchWatch = setMatchWatch;
    window.setCoefInOption = setCoefInOption;
    window.addCompletedMatch = addCompletedMatch;
    window.addOption = addOption;
    window.removeCurrentMatch = removeCurrentMatch;
    window.setMatchCoef = setCurrentMatchCoef;
    window.setCurrentMatch = setCurrentMatch;
    window.addCurrentMatch = addCurrentMatch;
    window.setCurMatches = setCurMatches;
    window.setComMatches = setComMatches;
    useEffect(() => {
        setIsSide(document.documentElement.clientWidth >= 1527)
        window.addEventListener('resize', (event) => {
            console.log('window.pageXOffset:',document.documentElement.clientWidth)
            setIsSide(document.documentElement.clientWidth >= 1527)
        });
        window.addEventListener('scroll', (event) => {
        
            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 95 ) {
                setSideIsFixed(true)
            } else setSideIsFixed(false)
        })
    }, [])
    return (

        <div className={styles.wrapper} style={
            (SideIsFixed && isSide) ? {marginLeft: 305} :{}
        }>
            <BetsSidebar
                setNeedUpdate = {setNeedUpdate}
            />
            <div className={styles.bets_main}>
                <div className={styles.bets__content}>
                    <Slider/>
                    <CurrentMatch
                        needUpdate = {needUpdate}
                        setNeedUpdate = {setNeedUpdate}
                        matches = {currentMatches}
                        selectedCoef = {selectedCoef}
                        setSelectedCoef = {setSelectedCoef}
                        location = {props.location}
                    />
                    <CompletedMatch matches={completedMatches}/>
                </div>
            </div>
            <div style={{
                position: 'sticky',
                top: 0,
            }}>
                <Inventory
                    setSelectedCoef = {setSelectedCoef}
                    currentMatches = {currentMatches}
                    setCurrentMatches = {setCurrentMatches}
                    selectedCoef = {selectedCoef}
                    isReg = {props.isReg}
                    setIsReg = {props.setIsReg}
                />
            </div>
            <Footer/>
        </div>
    )
}

export {AllBets};