import React, {useState, useEffect} from "react";

import styles from './match.module.css';

import twitch from './img/twitch.png'
import bottom_open from './img/bottom_open.png'

import arrow_down from './img/arrow.png'
import arrow_up from './img/arrow_up.png'

import arrow_green from './img/up.png'
import arrow_red from './img/down.png'

import watch_green from './img/watch_green.png'
import watch_grey from './img/watch_grey.png'
import watch_active from './img/watch_active.png'
import live from "../img/live.png";

import ReactTwitchEmbedVideo from "react-twitch-embed-video"


let Option = (e) => {
    let [isOpen, setIsOpen] = useState(false);
    useEffect(() => {
        if (isOpen) {
            setIsOpen(false)
        }
    }, [e.isOpen])
    return (
        <div className={styles.match__options}>
            {e.active_coef &&
            <div className={styles.option_win_line}
                 style={{left: e.active_coef === 'left' ? 0 : "", right: e.active_coef === 'right' ? 0 : "",
                     background: e.active_win ? "#399241" : "#A93B3D"}}>

            </div>
            }
            {e.active_coef &&
            <div
                 className={e.active_coef === 'left' ? styles.option_button_left : styles.option_button_right}
                 // onClick={() => setIsOpen(!isOpen)}
                 onMouseOver={() => setIsOpen(true)}
                 onMouseOut={(event) => {
                     event.persist()
                     setIsOpen(false)
                 }}
            >
                {e.active_win ? "+" : "-"}{e.weapons.length}

            </div>
            }
            {(e.active_coef && isOpen) &&
            // <div className={styles.option__list}>
            <div className={styles.option__list__wrapper} style={ e.active_coef === 'left' ? {left: 23} : {right: 23}}>
                {e.weapons.map(o => {
                    return <img src={require(`./img/weapons/${o}`)} alt="" className={styles.option__weapon}/>
                })}
            </div>
                // </div>
            }
            {e.active_coef &&
            <div className={e.active_coef === 'left' ? styles.option__title_left : styles.option__title_right}>
                <img src={require(`./img/weapons/${e.title_weapon}`)} alt=""/>
            </div>
            }
            <div className={styles.team_name} style={{justifyContent: "flex-end", marginRight: 16}}>{e.left_value}</div>
            <div className={styles.block__coef}
                 style={{background: e.selectedCoef === `left_${e.index}_${e.indexMatch}` ? "#399241" : e.active_coef === "left" ? e.active_win ? "#399241" : "#A93B3D" : ""}}
                 onClick={() => {
                     if (e.active_coef === null) {
                         console.log('YE BOY LEFT ', e.active_coef, e.index)
                         if (e.selectedCoef === `left_${e.index}_${e.indexMatch}`) e.setSelectedCoef(null)
                         else e.setSelectedCoef(`left_${e.index}_${e.indexMatch}`)
                     }
                 }}
            >
                {e.left_coef}
            </div>
            <div className={styles.match__result}>
                {e.center_text}
            </div>
            <div className={styles.block__coef}
                 style={{background: e.selectedCoef === `right_${e.index}_${e.indexMatch}` ? "#399241" : e.active_coef === "right" ? e.active_win ? "#399241" : "#A93B3D" : ""}}
                 onClick={() => {
                     if (e.active_coef === null) {
                         console.log('YE BOY RIGHT ', e.setSelectedCoef)
                         if (e.selectedCoef === `right_${e.index}_${e.indexMatch}`) e.setSelectedCoef(null)
                         else e.setSelectedCoef(`right_${e.index}_${e.indexMatch}`)
                     }
                 }}
            >
                {e.right_coef}
            </div>
            <div className={styles.team_name} style={{justifyContent: "flex-start", marginLeft: 16}}>{e.right_value}</div>
        </div>
    )
}

let BroadCast = (props) => {
    let [activeLanguage, setActiveLanguage] = useState("RU")
    useEffect(() => {
        if (props.display === "block") {
            var embed = new window.Twitch.Embed(`twitch-embed${props.index}`, {
                width: "100%",
                height: 600,
                channel: "monstercat",
                layout: "video",
                autoplay: false,
                // only needed if your site is also embedded on embed.example.com and othersite.example.com
                parent: ["embed.example.com", "othersite.example.com"]
            });
        
            embed.addEventListener(window.Twitch.Embed.VIDEO_READY, () => {
                var player = embed.getPlayer();
                player.play();
            });
        } else document.querySelector(`#twitch-embed${props.index}`).innerHTML = "";
    }, [props.display])
    return (
        <div style={{display: props.display}}>
            <div className={styles.language__switch}>
                <span style={activeLanguage === "RU" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("RU")}></span>
                <span style={activeLanguage === "EN" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("EN")}></span>
                <span style={activeLanguage === "SP" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("SP")}></span>
                <span style={activeLanguage === "BR" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("BR")}></span>
            </div>
            <div id={`twitch-embed${props.index}`}>
                {/* <img src={twitch} alt=""/> */}
            </div>
        </div>
    )
}

let Match = (props) => {
    let [hover, setHover] = useState(false);
    let [isOpen, setIsOpen] = useState(false);
    let [isOpenWeapon, setIsOpenWeapon] = useState(false);
    let [watchHover, setWatchHover] = useState(false)
    let [broadcast, setBroadcast] = useState(false);
    let ref_options = React.createRef();
    let ref__watch = React.createRef();
    useEffect(() => {
        console.log('NEEDUPDATE ',props.needUpdate)
        if (props.needUpdate === props.index || window.setNeedUpdate === props.index) {
            if (broadcast) {
                // handleBroadcast(false)
                setBroadcast(false)
                if (document.querySelectorAll(`.${styles.options_wrapper}`)[props.index]) document.querySelectorAll(`.${styles.options_wrapper}`)[props.index].style.height = 'auto';
                // ref_options.current.style.height = `${ref_options.current.scrollHeight}px`;
            } else {
                setTimeout(() => {
                    setBroadcast(false)
                }, 500)
                handleOption(!isOpen)
                setIsOpen(!isOpen)
            }
            props.setNeedUpdate(false);
            window.setNeedUpdate = null;
        }
    }, [props.needUpdate, window.setNeedUpdate]);
    let handleOption = (b) => {
        // console.table([ref_options.current])
        // console.log(ref_options.current.scrollHeight)
        // let timeout;
        if (b && !broadcast) {
            window.timeout = setTimeout(() => {
                console.log('index=',props.index)
                if (document.querySelectorAll(`.${styles.options_wrapper}`)[props.index]) document.querySelectorAll(`.${styles.options_wrapper}`)[props.index].style.overflow = 'visible';
            }, 500)
            ref_options.current.style.height = `${ref_options.current.scrollHeight}px`;
        }
        else {
            clearTimeout(window.timeout)
            ref_options.current.style.overflow = 'hidden';
            ref_options.current.style.height = `0px`;
            if (broadcast) setTimeout(() => {
                setIsOpen(false)
            }, 500)
        }
    }
    let handleBroadcast = (b) => {
        console.log(ref_options.current.style.height)
        if (b) {
            window.timeout = setTimeout(() => {
                console.log('index=',props.index)
                // document.querySelectorAll(`.${styles.options_wrapper}`)[props.index].style.overflow = 'visible';
            }, 500)
            if (!broadcast) ref_options.current.style.height = `${650}px`;
        }
        else {
            clearTimeout(window.timeout)
            ref_options.current.style.overflow = 'hidden';
            ref_options.current.style.height = `0px`;
            if (broadcast) setTimeout(() => {
                setIsOpen(false)
            }, 500)
        }
    }
    useEffect(() => {
        if (isOpen) {
            ref_options.current.style.height = `auto`;
            setTimeout(() => {
                ref_options.current.style.height = `${ref_options.current.scrollHeight}px`;
            }, 100)
            console.dir(ref_options.current)
            // alert('fuck');
        }
    }, [props.options.length])
    return (
        <a style={{marginBottom: 12, display: "block"}} name={props.index} >
            <div className={styles.match__one} style={{background: (isOpen || watchHover) ? "#39394a" : ""}} onClick={(event) => {
                if (isOpen) {
                    setTimeout(() => {
                        setBroadcast(false)
                    }, 500)
                }
                handleOption(!isOpen)
                setIsOpen(!isOpen)
            }} onMouseMove={() => setHover(true)} onMouseLeave={() => setHover(false)}>

                <div className={styles.match__team}
                     style={{background: "linear-gradient(270deg, rgba(47, 47, 63, 0.5) 0%, #4E426E 135.99%)",
                     opacity: isOpen ? 0 : 1}}>
                    <img src={require(`./img/teams/left.png`)} alt="" className={styles.match__img}/>
                </div>
                <div className={styles.match__team}
                     style={{background: "linear-gradient(270deg, rgba(59, 58, 79, 0.5) 0%, #4E426E 135.99%)",
                         opacity: (isOpen || hover) ? 1 : 0}}>
                    <img src={require(`./img/teams/left.png`)} alt="" className={styles.match__img}/>
                </div>
                <img src={bottom_open} alt="" className={styles.bottom_open} onClick={(event) => {
                    event.stopPropagation()
                    if (isOpen) {
                        setTimeout(() => {
                            setBroadcast(false)
                        }, 500)
                    }
                    handleOption(!isOpen)
                    setIsOpen(!isOpen)
                }}/>
                {/*<div className={styles.bottom_arrow}>*/}
                {/*    <span className={styles.line_arrow}></span>*/}
                {/*    <span className={styles.line_arrow}></span>*/}
                {/*</div>*/}
                <img src={arrow_down} alt="" className={styles.bottom_arrow}
                     style={{transform: !isOpen ? "translateX(-50%) rotate(0deg)" : " translateX(-50%) rotate(180deg)", opacity: !isOpen ? 1 : 0}}
                     onClick={(event) => {
                         event.stopPropagation()
                         if (isOpen) {
                             setTimeout(() => {
                                 setBroadcast(false)
                             }, 500)
                         }
                         handleOption(!isOpen)
                         setIsOpen(!isOpen)
                     }}
                />
                <img src={props.watch ? watch_green : watch_grey} alt="" className={styles.watch}
                     onMouseEnter={() => {
                            setWatchHover(true)
                            ref__watch.current.style.zIndex = 22;
                        }}
                     onMouseLeave={() => {
                         setWatchHover(false);
                        //  setTimeout(() => {
                            ref__watch.current.style.zIndex = 21;
                        //  }, 300)
                        }}
                     onClick={(event) => {
                         event.stopPropagation()
                         if (props.watch) {
                             if (isOpen && !broadcast) {
                                 handleBroadcast(true);
                                 setBroadcast(true);
                                 setIsOpen(true);
                             } else {
                                 handleBroadcast(!isOpen)
                                 if (isOpen) {
                                     setTimeout(() => {
                                         setBroadcast(false)
                                     }, 500)
                                 } else setBroadcast(!isOpen)
                                 // if (!isOpen) setBroadcast(!isOpen)
                                 // else setTimeout(() => {
                                 //     setBroadcast(!isOpen)
                                 // }, 500)
                                 setIsOpen(!isOpen)
                             }
                         }
                     }}
                />
                {/*{*/}
                {/*   <div alt="" className={styles.div_watch} style={{opacity: watchHover ? 1 : 0,*/}
                {/*       zIndex: watchHover ? 1 : -1,*/}
                {/*   }}      onMouseLeave={() => setWatchHover(false)}>*/}
                {/*       <img src={watch_active} alt=''/>*/}
                {/*   </div>*/}
                {/*}*/}
                <div className={styles.watch_mes} style={{background: "#2f2f3f", opacity: watchHover && props.watch ? 1 : 0}} ref={ref__watch}>
                     
                </div>
                <img src={arrow_up} alt="" className={styles.bottom_arrow}
                     style={{transform: isOpen ? "translateX(-50%) rotate(0deg)" : " translateX(-50%) rotate(-180deg)", opacity: isOpen ? 1 : 0}}
                     onClick={(event) => {
                         handleOption(!isOpen)
                         setIsOpen(!isOpen)
                     }}
                />
                <img src={require(`./img/teams/${props.game_logo}`)} alt="" className={styles.mini_game_logo}/>
                <div className={styles.team_name} style={{justifyContent: "flex-end"}}>
                    {props.left_name}
                </div>
                <div  className={styles.logo__team}>
                    <img src={require(`./img/teams/${props.left_logo}`)} alt=""/>
                </div>
                <div className={styles.block__coef} style={{color: 
                (isOpen && props.selectedCoef !== `left_${props.index}`) ? props.left_green ? "#43CA49" : "#F14843" : "",
                    background: (props.selectedCoef === `left_${props.index}` || props.active_coef === `left`) ? "#399241" : ""
                }}
                     onClick={(event) => {
                         event.stopPropagation();
                        console.log('YE BOY LEFT ', props.active_coef, props.index)
                         if (!props.active_coef) {
                             if (props.selectedCoef === `left_${props.index}`) props.setSelectedCoef(null)
                             else props.setSelectedCoef(`left_${props.index}`)
                         }
                     }}
                >
                    <img src={props.left_green ? arrow_green : arrow_red} alt=""className={styles.coef__arrow} style={{left: 18, 
                        opacity: (isOpen && props.selectedCoef !== `left_${props.index}`) ? 1 : 0}}/>
                    {props.left_coef}
                </div>
                {props.active_coef &&
                <div className={styles.option_win_line}
                     style={{left: props.active_coef === 'left' ? 0 : "", right: props.active_coef === 'right' ? 0 : "",
                         background: "#399241"}}>

                </div>
                }
                {props.active_coef &&
                <div
                    className={props.active_coef === 'left' ? styles.option_button_left_title : styles.option_button_right_title}
                    // onClick={() => setIsOpen(!isOpen)}
                    style={{top: 13, background: "#39394A", zIndex: 21}}
                    onMouseOver={() => setIsOpenWeapon(true)}
                    onMouseOut={(event) => {
                        setIsOpenWeapon(false)
                    }}
                >
                    {props.active_win ? "+" : "+"}{props.weapons.length}

                </div>
                }
                {(props.active_coef && isOpenWeapon) &&
                // <div className={styles.option__list}>
                <div className={styles.option__list__wrapper} style={ props.active_coef === 'left' ? {left: 23} : {right: 23}}>
                    {props.weapons.map(o => {
                        return <img src={require(`./img/weapons/${o}`)} alt="" className={styles.option__weapon}/>
                    })}
                </div>
                    // </div>
                }
                {props.active_coef &&
                <div className={props.active_coef === 'left' ? styles.option__title_left : styles.option__title_right} style={{
                    top: 13,
                    left: props.active_coef === 'left' ? 130 : "",
                    right: props.active_coef === 'right' ? 130 : "",
                }}>
                    <img src={require(`./img/weapons/${props.title_weapon}`)} alt=""/>
                </div>
                }
                <div className={styles.match__result}>
                    {props.live ? <div className={styles.live__button}>
                        <img src={live} alt=""/>
                        <span>Live</span>
                    </div> : props.date ?
                        <div className={styles.completed_date} style={{
                            flexDirection: "row",
                            position: "initial",
                            fontSize: 16,
                            // alignItems: 'flex-end',
                            // justifyContent: "flex-start",
                            // color: "white",
                            // fontWeight: "normal"
                        }}>
                            <div style={{marginRight: 4}}>{props.date.time}</div>
                            <div style={{fontSize: 14, whiteSpace: "nowrap"}}>{props.date.d}</div>
                        </div>
                         :props.center_text}
                </div>
                <div className={styles.block__coef} style={{color: 
                (isOpen && props.selectedCoef !== `right_${props.index}`) ? props.right_green ? "#43CA49" : "#F14843" : "",
                    background: (props.selectedCoef === `right_${props.index}` || props.active_coef === `right`) ? "#399241" : ""
                }}
                     onClick={(event) => {
                         event.stopPropagation();
                         console.log('OnClick = ', props.selectedCoef)
                        console.log('YE BOY RIGHT ', props.active_coef, props.index)
                         if (!props.active_coef) {
                             if (props.selectedCoef === `right_${props.index}`) props.setSelectedCoef(null)
                             else props.setSelectedCoef(`right_${props.index}`)
                         }
                     }}
                >
                    <img src={props.right_green ? arrow_green : arrow_red} alt="" className={styles.coef__arrow} style={{right: 18, 
                        opacity: (isOpen && props.selectedCoef !== `right_${props.index}`) ? 1 : 0}}/>
                    {props.right_coef}
                </div>
                <div  className={styles.logo__team}>
                    <img src={require(`./img/teams/${props.right_logo}`)} alt="" />
                </div>
                <div className={styles.team_name} style={{justifyContent: "flex-start"}}>{props.right_name}</div>

            </div>
            <div className={styles.options_wrapper} ref={ref_options} style={{overflow: "hidden"}}>
                {!broadcast && props.options.map((e, index) => {
                    return <Option
                        {...e} isOpen={!isOpen} index={index}
                        selectedCoef = {props.selectedCoef}
                        setSelectedCoef = {props.setSelectedCoef}
                        indexMatch = {props.index}
                    />
                })}
                {/*{broadcast && <BroadCast/>}*/}
                <BroadCast display={broadcast ? "block" : "none"} index={props.index}/>
            </div>
        </a>
    )
}

export {Match};