import React, {useEffect, useState} from 'react';

import s from './../AllBets/AllBets.module.css';
import styles from './Contact.module.css';

import {BetsSidebar} from './../BetsSidebar/BetsSidebar';
import {Inventory} from './../AllBets/parts/Inventory'
import {Footer} from './../Footer/Footer'
let Contact = (props) => {
    let [SideIsFixed, setSideIsFixed] = useState(false);
    let [isSide, setIsSide] = useState(true);   
    useEffect(() => {
        setIsSide(document.documentElement.clientWidth >= 1527)
        window.addEventListener('resize', (event) => {
            console.log('window.pageXOffset:',document.documentElement.clientWidth)
            setIsSide(document.documentElement.clientWidth >= 1527)
        });
        window.addEventListener('scroll', (event) => {

            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 95 ) {
                setSideIsFixed(true)
            } else setSideIsFixed(false)
        })
    }, [])
    return (
        <div className={s.wrapper}  style={
            (SideIsFixed && isSide) ? {marginLeft: 305} :{}
        }>
            <BetsSidebar/>
            <div className={s.bets_main}>
                <div className={s.bets__content}>
                    <div className={styles.wrapper}>
                        <div className={styles.header}>
                            
                        </div>
                        <div className={styles.container}>
                            <div className={styles.data__container}>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                         :
                                    </div>
                                    <div className={styles.data__right}>
                                        BETSHUB
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        :
                                    </div>
                                    <div className={styles.data__right}>
                                        Moscow
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                          :
                                    </div>
                                    <div className={styles.data__right}>
                                        01.01.1991
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        :
                                    </div>
                                    <div className={styles.data__right}>
                                        Altayskaya, bld. 4, appt. 95
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        :
                                    </div>
                                    <div className={styles.data__right}>
                                        Russia
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                         :
                                    </div>
                                    <div className={styles.data__right}>
                                        450 117
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        Email:
                                    </div>
                                    <div className={styles.data__right}>
                                        contact@betshub.com
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        Email  :
                                    </div>
                                    <div className={styles.data__right}>
                                        idea@betshub.com
                                    </div>
                                </div>
                                <div className={styles.line}/>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                         :
                                    </div>
                                    <div className={styles.data__right}>
                                        QIWI
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        :
                                    </div>
                                    <div className={styles.data__right}>
                                        Estonia
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                          :
                                    </div>
                                    <div className={styles.data__right}>
                                        01.01.1991
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        :
                                    </div>
                                    <div className={styles.data__right}>
                                        Altayskaya, bld. 4, appt. 95
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                         :
                                    </div>
                                    <div className={styles.data__right}>
                                        450 117
                                    </div>
                                </div>
                                <div className={styles.c_container}>
                                    <div className={styles.data__left}>
                                        Email:
                                    </div>
                                    <div className={styles.data__right}>
                                        contact@betshub.com
                                    </div>
                                </div>
                            </div>
                            <div className={styles.button__container}>
                                <div className={styles.button}> </div>
                                <div className={styles.button}> </div>
                                <div className={styles.button} style={{background: "#0088CC"}}>  </div>
                                <div className={styles.button} style={{background: "#0088CC"}}>  </div>
                                <div className={styles.button}  style={{background: "#3B5999"}}>  Facebook</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <Inventory selectedCoef = {null} isReg = {props.isReg}
                    setIsReg = {props.setIsReg}/>
            </div>
            <Footer/>
        </div>
    )
}

export {Contact}