let PageInventory = (props) => {
    let [items, setItems] = useState(myItems)
    let [closedItems, setClosedItems] = useState(new Set())
    let [selectAll, setSelectAll] = useState(false)
    let [selectedItems, setSelectedItems] = useState(new Set());
    let [sum, setSum] = useState(0)
    let [balance, setBalance] = useState(0)
    useEffect(() => {
        let summ = 0;
        selectedItems.forEach((value, valueAgain, set) => {
            summ += Number(items[value].price)
        })
        setSum(summ.toFixed(2));

    }, [selectedItems])
    useEffect(() => {
        let summ = 0;
        for (let i = 0; i < items.length; i++) {
            summ += Number(items[i].price);
        }
        setBalance(summ.toFixed(2));
    }, [])

    if (props.isReg) {
        return (
            <div className={styles.container} style={props.page === 1 ? {opacity: 1, display: 'block'} : {opacity: 0, display: 'none'}}>
            <div className={styles.balance__container}>
                {/* <div className={styles.balance__line}/> */}
                <div className={styles.balance__block}>
                    <div className={styles.balance__title}></div>
                    <div className={styles.balance__value}>${balance}</div>
                </div>
                {/* <div className={styles.balance__block}>
                    <div className={styles.balance__title}></div>
                    <div className={styles.balance__value}>${sum}</div>
                </div> */}
                <div className={styles.input__container}>
                    <div>
                         
                    </div>
                    <div className={selectAll ? styles.checkbox_true : styles.checkbox_false}
                         onClick={() => {
                            if (items.length === closedItems.size) return;
                             if (!selectAll) {
                                 let b = new Set();
                                 for (let i = 0; i < items.length; i++) {
                                     b.add(i)
                                 }
                                 setSelectedItems(b)
                             } else setSelectedItems(new Set())
                             setSelectAll(!selectAll)
                         }}>
                    </div>
                </div>
            </div>
            <div className={styles.item__container}>

                {items.length !== closedItems.size && items.map((e, index) => {
                    if (closedItems.has(index)) return;
                    return (
                        <div className={styles.item} style={ {
                            border: selectedItems.has(index) ? `2px solid rgba(252, 185, 11, 0.6)` : "",
                            background:
                                e.rarity === 1 ? "radial-gradient(50% 50% at 0% 0%, rgba(94, 152, 217, 0.05) 0%, rgba(94, 152, 217, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(94, 152, 217, 0.1) 0%, rgba(94, 152, 217, 0) 100%), #2F2F3F" :
                                e.rarity === 2 ? "radial-gradient(50% 50% at 0% 0%, rgba(136, 71, 255, 0.05) 0%, rgba(136, 71, 255, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(136, 71, 255, 0.1) 0%, rgba(136, 71, 255, 0) 100%), #2F2F3F" :
                                e.rarity === 3 ? "radial-gradient(50% 50% at 0% 0%, rgba(235, 75, 75, 0.05) 0%, rgba(235, 75, 75, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(235, 75, 75, 0.1) 0%, rgba(235, 75, 75, 0) 100%), #2F2F3F" :
                                                 "radial-gradient(50% 50% at 0% 0%, rgba(228, 174, 57, 0.05) 0%, rgba(228, 174, 57, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(228, 174, 57, 0.1) 0%, rgba(228, 174, 57, 0) 100%), #2F2F3F"
                        }}
                             onClick={() => {
                                 let temp = new Set(selectedItems);
                                 if (temp.has(index)) {
                                     temp.delete(index)
                                 } else temp.add(index)
                                 setSelectedItems(temp)
                             }}>
                            <img src={require(`./img/weapons/${e.photo}`)} alt="" className={styles.item__photo} style={{marginTop: 0}}/>
                            <div className={styles.item__price} style={{top: 125, width: '100%', left: 0, textAlign: 'center'}}>${e.price}</div>
                            <div className={styles.item__text} style={{top: 90}}>
                                <span style={{
                                    width: 95,
                                    textOverflow: "ellipsis",
                                    whiteSpace : "nowrap",
                                    overflow: "hidden"
                                }}>{e.text_1}</span>
                                <span>{e.text_2}</span>
                            </div>
                        </div>
                    )
                })}
                {items.length === closedItems.size &&
                    <div className={styles.no__item__container}>
                        <img src={recharge} style={{transform: "translateY(-10px)"}}/>
                        <div className={styles.no__item__title}>  , </div>
                    </div>
                }
            </div>
            { selectedItems.size !== 0 && 
                <div className={styles.pot__bet}>
                    <div className={styles.pot__line}>
                        <span></span>
                        <span style={{fontSize: 18, color: "#FFF", fontWeight: "500"}}>{sum}$</span>
                    </div>
                    <div className={styles.pot__line}>
                        <span></span>
                        <span style={{fontSize: 18, color: "#43CA49", fontWeight: "500"}}>55.33$</span>
                    </div>
                </div>
            }
            <div className={styles.button} style={
                props.selectedCoef === null ? {
                    border: '1px solid #595D6E',
                    background: '#2F2F3F',
                    color: "#595D6E",
                    cursor: "initial"
                } : {}
            } onClick={() => {
                if (props.selectedCoef && selectedItems.size > 0) {
                    console.log('seletedCoef:',props.selectedCoef)
                    let pos = props.selectedCoef.slice(0, props.selectedCoef.indexOf("_"))
                    console.log('pos = ',pos)
                    // let indexOption = props.selectedCoef.slice(props.selectedCoef.indexOf("_"), props.selectedCoef.indexOf("_", props.selectedCoef.indexOf("_")))
                    console.log('length = ',props.selectedCoef.split("_").length - 1)
                
                    if (props.selectedCoef.split("_").length - 1 === 1) {
                        let indexMatch = props.selectedCoef.slice(props.selectedCoef.indexOf("_") + 1, props.selectedCoef.length)
                        let temp = [...props.currentMatches];
                        console.log('indexMatch = ',indexMatch)
                        temp[indexMatch].title_weapon = '1.png';
                        let newWeapon = [];
                        for (let i = 0; i < selectedItems.size; i++) {
                            newWeapon.push("1.png")
                        }
                        temp[indexMatch].weapons = newWeapon;
                        temp[indexMatch].active_coef = pos;
                        props.setCurrentMatches(temp);
                        props.setSelectedCoef(null);
                        let s = closedItems;
                        selectedItems.forEach(value => s.add(value));
                        console.log('CloseItems:', s)
                        setSelectedItems(new Set());

                    } else {
                        let indexOption = props.selectedCoef.slice(props.selectedCoef.indexOf("_") + 1, props.selectedCoef.indexOf("_", props.selectedCoef.indexOf("_") + 1))

                        let indexMatch = props.selectedCoef.slice(props.selectedCoef.indexOf("_", props.selectedCoef.indexOf("_") + 1) + 1, props.selectedCoef.length)

                        console.log('indexMatch:',indexMatch)

                        console.log('indexOption:',indexOption)
                        let temp = [...props.currentMatches];
                        temp[indexMatch].options[indexOption].active_coef = pos;
                        temp[indexMatch].options[indexOption].title_weapon = '1.png';
                        let newWeapon = [];
                        for (let i = 0; i < selectedItems.size; i++) {
                            newWeapon.push("1.png")
                        }
                        temp[indexMatch].options[indexOption].weapons = newWeapon;
                        console.log('temp=',temp)
                        props.setCurrentMatches(temp);
                        props.setSelectedCoef(null);

                        let s = closedItems;
                        selectedItems.forEach(value => s.add(value));
                        console.log('CloseItems:', s)
                        setSelectedItems(new Set());
                    }
                }
            }}>
                 
            </div>
        </div>
        )
    } else {
        return (
        <div className={styles.steam__container} style={props.page === 1 ? {opacity: 1, display: 'flex'} : {opacity: 0, display: 'none'}}>
            <img src={no__reg} className={styles.reg__no}/>
            <div className={styles.reg__title}>,   </div>
            <div className={styles.reg__button} onClick={() => props.setIsReg(true)}>
                <span>  STEAM</span>
                <img src={steam} className={styles.reg__steam}/>
            </div>
        </div>
    )
    }
}

let PageShop = (props) => {
    let [selectAll, setSelectAll] = useState(false)
    let [selectedItems, setSelectedItems] = useState(new Set());
    let [filter, setFilter] = useState("")
    let [priceFilter, setPriceFilter] = useState(null)
    let [sum, setSum] = useState(0)
    useEffect(() => {
        let summ = 0;
        selectedItems.forEach((value, valueAgain, set) => {
            summ += Number(shop[value].price)
        })
        setSum(summ.toFixed(2));

    }, [selectedItems])
    if (props.isReg) {
        return (
            // {
                <div className={styles.container} style={props.page === 2 ? {opacity: 1, display: 'block'} : {opacity: 0, display: 'none'}}>
                <div className={styles.balance__container}>
                    <div className={styles.balance__line}/>
                    <div className={styles.balance__block}>
                        <div className={styles.balance__title}></div>
                        <div className={styles.balance__value}>$55.05</div>
                    </div>
                    <div className={styles.balance__block}>
                        <div className={styles.balance__title}></div>
                        <div className={styles.balance__value}>${sum}</div>
                    </div>
                    <div className={styles.input__container}>
                        <div>
                             
                        </div>
                        <div className={selectAll ? styles.checkbox_true : styles.checkbox_false}
                            onClick={() => {
                                if (!selectAll) {
                                    let b = new Set();
                                    for (let i = 0; i < shop.length; i++) {
                                        b.add(i)
                                    }
                                    setSelectedItems(b)
                                } else setSelectedItems(new Set())
                                setSelectAll(!selectAll)
                            }}>
                        </div>
                    </div>
                </div>
                <div className={styles.input__wrapper}>
                    <input type="text" placeholder='' className={styles.input_1} onChange={(event) => setFilter(event.target.value)}/>
                    <input type="text" placeholder='' className={styles.input_2} onChange={(event) => setPriceFilter(Number(event.target.value))}/>
                </div>
                <div className={styles.item__container}>

                    {shop.map((e, index) => {
                        if (e.text_1.toLocaleLowerCase().indexOf(filter.toLocaleLowerCase()) === -1
                            && e.text_2.toLocaleLowerCase().indexOf(filter.toLocaleLowerCase()) === -1) return;
                        if (priceFilter) if (Number(e.price) > priceFilter) return;
                        return (
                            <div className={styles.item} style={{
                                border: selectedItems.has(index) ? `2px solid rgba(252, 185, 11, 0.6)` : "",
                                background:
                                    e.rarity === 1 ? "radial-gradient(50% 50% at 0% 0%, rgba(94, 152, 217, 0.05) 0%, rgba(94, 152, 217, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(94, 152, 217, 0.1) 0%, rgba(94, 152, 217, 0) 100%), #2F2F3F" :
                                    e.rarity === 2 ? "radial-gradient(50% 50% at 0% 0%, rgba(136, 71, 255, 0.05) 0%, rgba(136, 71, 255, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(136, 71, 255, 0.1) 0%, rgba(136, 71, 255, 0) 100%), #2F2F3F" :
                                    e.rarity === 3 ? "radial-gradient(50% 50% at 0% 0%, rgba(235, 75, 75, 0.05) 0%, rgba(235, 75, 75, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(235, 75, 75, 0.1) 0%, rgba(235, 75, 75, 0) 100%), #2F2F3F" :
                                                    "radial-gradient(50% 50% at 0% 0%, rgba(228, 174, 57, 0.05) 0%, rgba(228, 174, 57, 0) 100%), radial-gradient(100% 100% at 100% 100%, rgba(228, 174, 57, 0.1) 0%, rgba(228, 174, 57, 0) 100%), #2F2F3F"

                            }}
                                onClick={() => {
                                    let temp = new Set(selectedItems);
                                    if (temp.has(index)) {
                                        temp.delete(index)
                                    } else temp.add(index)
                                    setSelectedItems(temp)
                                }}>
                                    <div className={styles.item__top} style={{top: 10, left: 10}}>
                                FT <span style={{marginLeft: 8, color: "#E5864E"}}>ST</span>
                            </div>
                                <img src={require(`./img/weapons/${e.photo}`)} alt="" className={styles.item__photo} style={{marginTop: 0}}/>
                                <div className={styles.item__price} style={{top: 125, width: '100%', left: 0, textAlign: 'center'}} >${e.price}</div>
                                {/* style={{top: 125, width: '100%', left: 0, textAlign: 'center'}} */}
                                <div className={styles.item__text} style={{top: 90}}>
                                    <span style={{
                                        width: 95,
                                        textOverflow: "ellipsis",
                                        whiteSpace : "nowrap",
                                        overflow: "hidden"
                                    }}>{e.text_1}</span>
                                    <span>{e.text_2}</span>
                                </div>
                            </div>
                        )
                    })}

                </div>
                <div className={styles.button}
                    style={
                        selectedItems.size === 0 ? {
                            border: '1px solid #595D6E',
                            background: '#2F2F3F',
                            color: "#595D6E",
                            cursor: "initial"
                        } : {}
                    }>
                    
                </div>
            </div>
            // }
        )
    } else {
        return (
        <div className={styles.steam__container} style={props.page === 2 ? {opacity: 1, display: 'flex'} : {opacity: 0, display: 'none'}}>
            <img src={no__reg} className={styles.reg__no}/>
            <div className={styles.reg__title}>,   </div>
            <div className={styles.reg__button} onClick={() => props.setIsReg(true)}>
                <span>  STEAM</span>
                <img src={steam} className={styles.reg__steam}/>
            </div>
        </div>
    )
    }
}

let Inventory = (props) => {
    let [isFixed, setIsFixed] = useState(false);
    useEffect(() => {
        window.addEventListener('scroll', (event) => {
            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 110) {
                setIsFixed(true)
            } else setIsFixed(false)
        })

    }, [])
    let [page, setPage] = useState(1);
    useEffect(() => {
        setPage(1);
    }, [props.selectedCoef])
    return (
        <div className={styles.inventory__wrapper}style={
            isFixed ? {
                position: 'fixed',
                top: 0,
                marginTop: 5,
            } : {}
        }>
            <div className={styles.switcher}>
                <div className={styles.switcher__item}
                     onClick={() => setPage(1)}
                     style={{
                    borderBottom: `2px solid ${page === 1 ? "#FCB90B" : "#1F1F29"}`,
                    color: `${page === 1 ? "#FCB90B" : "#8C90A4"}`
                }}>
                    <img src={inventory_grey} alt="" className={styles.switcher__icon} style={{opacity: page === 1 ? 0 : 1}}/>
                    <img src={inventory_yellow} alt="" className={styles.switcher__icon} style={{opacity: page === 1 ? 1 : 0}}/>
                    <span className={styles.switcher__text}></span>
                </div>
                <span className={styles.menu__line}></span>
                <div className={styles.switcher__item}
                    onClick={() => setPage(2)}
                     style={{
                    borderBottom: `2px solid ${page === 2 ? "#FCB90B" : "#1F1F29"}`,
                    color: `${page === 2 ? "#FCB90B" : "#8C90A4"}`
                }}>
                    <img src={shop_grey} alt="" className={styles.switcher__icon} style={{opacity: page === 2 ? 0 : 1}}/>
                    <img src={shop_yellow} alt="" className={styles.switcher__icon} style={{opacity: page === 2 ? 1 : 0}}/>
                    <span className={styles.switcher__text}> </span>
                </div>
            </div>
            <PageInventory page={page}
               selectedCoef = {props.selectedCoef}
               currentMatches = {props.currentMatches}
               setCurrentMatches = {props.setCurrentMatches}
               setSelectedCoef = {props.setSelectedCoef}
               isReg = {props.isReg}
               setIsReg = {props.setIsReg}
            />
            <PageShop page={page}
               selectedCoef = {props.selectedCoef}
               isReg = {props.isReg}
               setIsReg = {props.setIsReg}
            />


        </div>
    )
}

export {Inventory}