import React, {useEffect, useState} from 'react';

import s from './../AllBets/AllBets.module.css'
import styles from './Statistic.module.css';

import {BetsSidebar} from "../BetsSidebar/BetsSidebar";
import {Inventory} from "../AllBets/parts/Inventory";
import {Footer} from "../Footer/Footer";
import search from "../AllBets/parts/img/search.png";

import cs_orange from './img/cs_orange.png'
import dota_grey from './img/dota_grey.png'
import navi from './img/navi.png'
import navi__big from './img/navi__big.png'
import windigo from './img/windigo.png'


let result = [
    true, false, true, false, true, true, true, true,
]


let Statisctic = (props) => {
    let [SideIsFixed, setSideIsFixed] = useState(false);
    let [isSide, setIsSide] = useState(true);

    let [page, setPage] = useState(1);
    let [secondPage, setSecondPage] = useState(1)
    let [activeTime, setActiveTime] = useState(1);
    let [filter, setFilter] = useState("")
    useEffect(() => {
        setIsSide(document.documentElement.clientWidth >= 1527)
        window.addEventListener('resize', (event) => {
            console.log('window.pageXOffset:',document.documentElement.clientWidth)
            setIsSide(document.documentElement.clientWidth >= 1527)
        });
        window.addEventListener('scroll', (event) => {

            let scrollTop = window.pageYOffset ? window.pageYOffset : (document.documentElement.scrollTop ? document.documentElement.scrollTop : document.body.scrollTop);
            if (scrollTop > 95 ) {
                setSideIsFixed(true)
            } else setSideIsFixed(false)
        })
    }, [])
    return (
        <div className={s.wrapper} style={
            (SideIsFixed && isSide) ? {marginLeft: 305} :{}
        }>
            <BetsSidebar/>
            <div className={s.bets_main}>
                <div className={s.bets__content}>
                    <div className={styles.stat__container}>
                        <div className={styles.stat__header}>
                            <div className={styles.header__title}>
                                {secondPage === 1 ? <span>   </span> : ""}
                                {secondPage === 2 ? <span>
                                                        <span className={styles.header__line} onClick={() => setSecondPage(1)}>   
                                                            &nbsp;/ {page === 1 ? "CS:GO" : "DOTA 2"}&nbsp; </span> / &nbsp;NATUS VINCERE
                                                    </span> : ""}
                            </div>
                            <div className={styles.input__wrapper}>
                                <img src={search} alt="" className={styles.title__search}/>
                                <input type="text" className={styles.title__input} placeholder=" /" style={{width: 200}}
                                       onChange={(event) => setFilter(event.target.value)}/>
                            </div>
                        </div>
                        {secondPage === 1 &&
                            <div className={styles.team__container}>
                                <div className={styles.team} onClick={() => setPage(1)} style={{
                                    borderBottom: page === 1 ? "1px solid #FCB90B" : "1px solid #252632"
                                }}>
                                    <img src={cs_orange} style={{
                                        // filter: 'invert(34%) sepia(11%) saturate(635%) hue-rotate(191deg) brightness(101%) contrast(92%)',
                                        filter: page === 2 ? 'invert(10%) sepia(20%) saturate(370%) hue-rotate(187deg) brightness(45%) contrast(50%)' : ""
                                    }}/>
                                    <span style={{
                                        color: page === 1 ? "#FFF" : "#595D6E",
                                    }}>CS:GO</span>
                                </div>
                                <div className={styles.team} onClick={() => setPage(2)} style={{
                                    borderBottom: page === 2 ? "1px solid #FCB90B" : "1px solid #252632"
                                }}>
                                    <img src={dota_grey} style={{
                                        filter: page === 2 ? "invert(95%) sepia(0%) saturate(1800%) hue-rotate(354deg) brightness(115%) contrast(98%)" : ""
                                    }}/>
                                    <span style={{color: page === 2 ? "#FFF" : "#595D6E"}}>DOTA 2</span>
                                </div>
                            </div>
                        }
                        {secondPage === 2 &&
                            <div className={styles.team__container}>
                                <div className={styles.time} style={{
                                    color: activeTime === 1 ? "#FFF" : "#595D6E",
                                    borderBottom: `2px solid ${activeTime === 1 ? "#FCB90B" : "#252632"}`
                                }} onClick={() => setActiveTime(1)}> </div>
                                <div className={styles.time} style={{
                                    color: activeTime === 2 ? "#FFF" : "#595D6E",
                                    borderBottom: `2px solid ${activeTime === 2 ? "#FCB90B" : "#252632"}`
                                }}  onClick={() => setActiveTime(2)}> </div>
                                <div className={styles.time} style={{
                                    color: activeTime === 3 ? "#FFF" : "#595D6E",
                                    borderBottom: `2px solid ${activeTime === 3 ? "#FCB90B" : "#252632"}`
                                }}  onClick={() => setActiveTime(3)}>  </div>
                            </div>
                        }
                        { secondPage === 1 &&
                            <div className={styles.teams}>
                                { ['1', '1', '1', '1', '1', '1', '1', '1', '1'].map(() => {
                                    if ("natus vincere".indexOf(filter.toLowerCase()) === -1) return;
                                    return (
                                        <div className={styles.t} onClick={() => setSecondPage(2)}>
                                            <div className={styles.t__number}>
                                                1
                                            </div>
                                            <div className={styles.t__team}>
                                                <img src={navi}/>
                                            </div>
                                            <div className={styles.t__name}>
                                                NATUS VINCERE
                                            </div>
                                            <div className={styles.t__countBet}>
                                                189 774 
                                            </div>
                                        </div>
                                    )
                                })
                                }
                            </div>
                        }
                        {   secondPage === 2 &&
                                <div className={styles.teams} style={{display: "flex"}}>
                                    <div className={styles.team__info}>
                                        <div className={styles.info__header}> </div>
                                        <div className={styles.info__container}>
                                            <div className={styles.team__logo}>
                                                <img src={navi__big}/>
                                            </div>
                                            <div className={styles.info__name}>
                                                NATUS VINCERE
                                            </div>
                                            <div className={styles.info__min_c}>
                                                <div className={styles.min__line}>
                                                    <span className={styles.line__left}> </span>
                                                    <span className={styles.line__right}>14 774</span>
                                                </div>
                                                <div className={styles.min__line}>
                                                    <span className={styles.line__left}></span>
                                                    <span className={styles.line__right}>10 743</span>
                                                </div>
                                                <div className={styles.min__line}>
                                                    <span className={styles.line__left}></span>
                                                    <span className={styles.line__right}>887</span>
                                                </div>
                                                <div className={styles.min__line}>
                                                    <span className={styles.line__left}></span>
                                                    <span className={styles.line__right}>10 792</span>
                                                </div>
                                                <div className={styles.min__line}>
                                                    <span className={styles.line__left}></span>
                                                    <span className={styles.line__right} style={{color: "#4cff53"}}>58 %</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className={styles.team__res}>
                                        <div className={styles.header__res}>
                                            <span> </span>
                                            <span></span>
                                            <span style={{textAlign: "center"}}></span>
                                            <span style={{textAlign: "end"}}></span>
                                        </div>
                                        {
                                            result.map(e => {
                                                return (
                                                    <div className={e ? styles.res__container_win : styles.res__container_lose}>
                                                        <span>30.09.2019&nbsp;&nbsp;&nbsp;&nbsp;01:45</span>
                                                        <span style={{display: "flex", alignItems: "center"}}>
                                                            <img src={windigo} style={{marginRight: 18}}/>
                                                            <div>Windigo</div>
                                                        </span>
                                                        <span style={{textAlign: 'center', fontWeight: "bold", color: "#8C90A4"}}>
                                                            88%
                                                        </span>
                                                        <span style={{ display: "flex", justifyContent : "flex-end", alignItems: "center", height: "100%"}}>
                                                            <span style={{color: "#4CFF53", display: "flex", alignItems: "center", height: '100%'}}>16&nbsp;</span>
                                                            <span style={{color: "#8C90A4", display: "flex", alignItems: "center", height: '100%', transform: "translateY(-2px)"}}>:</span>
                                                            <span style={{color: "#f14843", display: "flex", alignItems: "center", height: '100%'}}>&nbsp;7</span>
                                                        </span>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                        }
                    </div>
                </div>
            </div>
            <div>
                <Inventory selectedCoef = {null} isReg = {props.isReg}
                    setIsReg = {props.setIsReg}/>
            </div>
            <Footer

            />
        </div>
    )
}

export {Statisctic}